/* weather.css */

.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .input-wrapper {
    margin-bottom: 20px;
  }
  
  .input-wrapper input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
  }
  
  .weather-info {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 5px;
  }
  
  .weather-info h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .weather-info p {
    font-size: 18px;
    margin-bottom: 8px;
  }
  